@import url(https://fonts.googleapis.com/css2?family=Recursive&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main {
    width: 100%;
    height: 100vh;
    background-color: #254b62;
    overflow: auto;
}

@media screen and (min-width: 768px) {
    .main {
        padding: 0 20vw;
    }
}

@media screen and (min-width: 1440px) {
    .main {
        padding: 0 30vw;
    }
}

.top {
    background-color: #1d3e53;
    color: whitesmoke;
    padding: 15px;
    border-radius: 20px;
    font-family: 'Recursive', sans-serif;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;

}

.header {
    background-color: #1d3e53;
    color: whitesmoke;
    margin: 10px 0 0 10px;
    font-size: 25px;
    border-radius: 20px 20px 0 0;
    font-family: 'Recursive', sans-serif;

    cursor: pointer;
    border-bottom: whitesmoke 3px solid;
}

.day {
    padding: 15px;
    color: whitesmoke;
    font-family: 'Recursive', sans-serif;
    font-size: 24px;
    font-weight: 600;
}

.temp {
    padding: 15px;
    color: whitesmoke;
    font-family: 'Recursive', sans-serif;
    font-size: 18px;
}

.flex {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

@media screen and (max-width: 768px) {
    .flex {
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: unset;
                justify-content: unset;
    }

    .flex p {
        margin-bottom: 0;
    }
}

.sunrise-sunset {
    padding: 15px;
    color: whitesmoke;
    font-family: 'Recursive', sans-serif;
    font-size: 16px;
}

.description {
    padding: 15px;
    color: whitesmoke;
    font-family: 'Recursive', sans-serif;
    font-size: 24px;
    font-weight: 600;
}

.button.circular.inverted {
    width: 35px;
    height: 35px;
}

.ui.inverted.circular.button {
    color: #77abb7 !important;
    box-shadow: 0 0 0 2px currentColor inset !important;
}


.weather-icon {
    margin-top: 2em;
    width: 50%;
    max-width: 200px;
    -webkit-animation: icon-animation 10s ease-in-out infinite;
            animation: icon-animation 10s ease-in-out infinite;
}

@-webkit-keyframes icon-animation {
    0% {
        -webkit-transform: translateX(0) scale(1);
                transform: translateX(0) scale(1);
    }
    25% {
        -webkit-transform: translateX(5px) scale(1.05);
                transform: translateX(5px) scale(1.05);
    }
    50% {
        -webkit-transform: translateX(0) scale(1);
                transform: translateX(0) scale(1);
    }
    75% {
        -webkit-transform: translateX(-5px) scale(1.05);
                transform: translateX(-5px) scale(1.05);
    }
    100% {
        -webkit-transform: translateX(0px) scale(1);
                transform: translateX(0px) scale(1);
    }

}

@keyframes icon-animation {
    0% {
        -webkit-transform: translateX(0) scale(1);
                transform: translateX(0) scale(1);
    }
    25% {
        -webkit-transform: translateX(5px) scale(1.05);
                transform: translateX(5px) scale(1.05);
    }
    50% {
        -webkit-transform: translateX(0) scale(1);
                transform: translateX(0) scale(1);
    }
    75% {
        -webkit-transform: translateX(-5px) scale(1.05);
                transform: translateX(-5px) scale(1.05);
    }
    100% {
        -webkit-transform: translateX(0px) scale(1);
                transform: translateX(0px) scale(1);
    }

}
